import React from 'react';
import { Redirect } from "react-router-dom";
import Home from '../pages/home/Home';
import BBpage from './../pages/BBpage/BBpage'
import Layout from '../layout/userLayout';
export default [
    {
        path: '/',
        component: Layout,
        routes: [
            {
                path: "/",
                exact: true,
                render: () => (
                    <Redirect to={"/home"} />
                )
            },
            {
                path: "/home",
                component: Home
            },
            {
                path: "/bbpage",
                component: BBpage
            },
        ]
    }
]