import React, { useState } from 'react';
import { Button,Affix } from 'antd';
import './header.scss'; 
export default ()=>{
    return (
        <Affix offsetTop={0}>
            <div className='header'>
                <div className='header_box clearfix'>
                <div className='header_logo'>
                    <img alt='' src={require('./../../images/logo.png')} onClick={()=>{window.location.href='#/'}} />
                </div>
                <div className='header_nav'>
                    <span onClick={()=>{window.location.href='#/'}}>
                        首页    
                    </span>
                    <span onClick={()=>{window.location.href='#/bbpage'}}>
                        逼逼叨叨
                    </span>
                    <span>
                    个人空间
                    </span>
                </div>
                </div>
            </div>
        </Affix>
    )
}