function get(url, callback, headers, isWithoutToken = false) {
    if (!headers) {
        headers = {};
    }
    headers = Object.assign({
        'Accept': 'application/json', // 通过头指定，获取的数据类型是JSON
        'Content-Type': 'application/json',
    }, headers);
    if (!isWithoutToken) {
        headers['accessToken'] = localStorage.getItem('access_token');
        if (headers['accessToken'] === null) {
            headers['accessToken'] = ''
        }
    }
    let fetchOptions = {
        method: "GET",
        headers: headers,
    };

    fetch(url, fetchOptions).then((response) => response.json())
        .then((obj) => {
            if (obj.code == 530) {  //未登录

            } else {
                callback(obj.data);
            }
        })
}

export default {
    get
}